import React from 'react';
import { reduxForm } from 'redux-form';
import { maxLen, required } from '../../utils/validators/validators';
import {
  createField,
  Input,
} from '../common/FormControls/FormControls';
import { connect } from 'react-redux';
import { login } from '../../redux/auth-reducer';
import { Navigate } from 'react-router-dom';
import { compose } from 'redux';
import Error from '../Error/Error';

let LoginForm = ({ handleSubmit, error, maxLen, captcha }) => {
  return (
    <form onSubmit={handleSubmit}>
      {error && <Error text={error} />}
      <div className="mt-20 flex justify-center flex-wrap">
        <div>
          <div className="text-white font-bold text-center mb-2">
            Log into InLink
          </div>
          <div className={'mb-2 text-black'}>
            {createField(
              'Email',
              'email',
              Input,
              [required, maxLen],
              null,
            )}
          </div>
          <div className="mb-2 text-black">
            {createField(
              'Password',
              'password',
              Input,
              [required, maxLen],
              'password',
            )}
          </div>
          <div className="mb-2">
            {createField(
              'RememberMe',
              'rememberMe',
              Input,
              null,
              'checkbox',
            )}
            <span className="ml-1 text-white">remember me</span>
          </div>
          <div>
            <button className="text-gray-900 text-center bg-gray-300 border border-gray-400 rounded relative w-full">
              Log in
            </button>
          </div>
          <div className="text-white mt-2">
            Free account for visitors:
            <div>Email: free@samuraijs.com</div>{' '}
            <div>Password: free</div>
          </div>

          {captcha && (
            <div className={'mt-2 text-black'}>
              <img src={captcha} alt={'captcha'} className={'mb-1'} />
              {createField(
                'Captcha',
                'captcha',
                Input,
                [required, maxLen],
                null,
              )}
            </div>
          )}
        </div>
      </div>
    </form>
  );
};

LoginForm = reduxForm({
  form: 'login',
})(LoginForm);

